html {
  position: relative;
  min-height: 100%;
}
.row-gap {
  margin-top: 50px;
}
.search-container {
  padding-top: 16px;
  padding-bottom: 16px;
}
.main-container {
  padding-bottom: 75px;
}
.navbar-logo {
  margin: 8px 20px 0 0;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #002440;
  color: #e3ebf1;
  font-size: 0.70em;
  padding-top: 60px;
}
.footer a {
  color: #e3ebf1;
}
body {
  margin-bottom: 520px;
}
@media (min-width: 992px) {
  body {
    margin-bottom: 330px;
  }
}
.footer {
  height: 520px;
}
@media (min-width: 992px) {
  .footer {
    height: 330px;
  }
}
.borderless > tbody > tr > td {
  border: none;
}
.no-clickonce {
  display: none;
}
.tag {
  background-color: #eff9ff;
  padding: 2px 10px;
  line-height: 2em;
  margin: 0 2px;
}
.banner {
  padding: 10px;
}
.banner.banner-bright {
  background: yellow;
  font-size: 1.25em;
}
.banner.banner-info {
  background: #f4f4f4;
}
.ms-Icon-ul {
  margin-left: 24px;
}
.ms-Icon-ul li {
  position: relative;
}
.ms-Icon-ul li i.ms-Icon {
  position: absolute;
  left: -24px;
}
.page-home .jumbotron h1 {
  margin: 32px;
}
.page-home .what-is-nuget {
  font-size: 1.15em;
  padding-bottom: 75px;
}
.page-home .triangle:after {
  content: "";
  position: absolute;
  bottom: -7px;
  border-width: 7px 7px 0;
  border-style: solid;
  border-color: #fff transparent;
  display: block;
  width: 0;
}
.page-home .triangle-left:after {
  left: 15px;
}
.page-home .triangle-right:after {
  right: 15px;
}
.page-home .circuit-board {
  margin-top: 50px;
  width: 804px;
  height: 471px;
  position: relative;
  margin: 0 auto;
  background-image: url('../img/circuit-board.png');
  background-repeat: no-repeat;
  background-position: center bottom;
}
.page-home .circuit-board .circuit-board-label {
  position: absolute;
  height: 75px;
  background-color: #fff;
  color: #000;
  box-shadow: 0 0 3px black;
}
.page-home .circuit-board .circuit-board-label h3,
.page-home .circuit-board .circuit-board-label h5 {
  margin: 9px 0 0 0;
}
.page-home .circuit-board .circuit-board-pd {
  left: 167px;
  top: 0;
  float: left;
  width: 200px;
}
.page-home .circuit-board .circuit-board-pv {
  left: 3px;
  top: 173px;
  float: left;
  width: 144px;
}
.page-home .circuit-board .circuit-board-up {
  right: 3px;
  top: 159px;
  float: right;
  width: 144px;
}
.page-list-packages h1 {
  margin-bottom: 33px;
}
.page-list-packages .packages {
  border-top-style: solid;
  border-top-width: 1px;
  border-top-color: #dbdbdb;
}
.page-list-packages .packages .package {
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #dbdbdb;
  font-size: 15px;
}
.page-list-packages .packages .package img {
  margin-left: auto;
  margin-right: auto;
  max-width: 50px;
  max-height: 50px;
}
.page-list-packages .packages .package .package-header h2 {
  display: inline;
  font-weight: 300;
}
.page-list-packages .packages .package .package-header .package-by {
  margin-left: 10px;
  color: #000;
}
.page-list-packages .packages .package .package-statistics {
  margin-top: 10px;
  line-height: 20px;
  color: #666666;
}
.page-list-packages .packages .package .package-statistics .package-statistics-middle {
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-left-style: solid;
  border-right-style: solid;
  border-width: 1px;
  border-color: #dbdbdb;
}
.page-list-packages .packages .package .package-details {
  margin-top: 10px;
  color: #000;
  line-height: 20px;
}
.page-list-packages .packages .package .package-tags {
  margin-top: 10px;
  margin-left: -2px;
}
.page-list-packages .pager {
  margin-top: 75px;
  margin-bottom: 0px;
  float: right;
}
.page-package-details h4 > span {
  border-bottom: 1px solid #d8d8d8;
  margin-bottom: 3px;
}
.page-package-details .dependency-group li span {
  color: #666;
}
.page-package-details .no-border {
  border: 0;
}
.page-package-details #show-hidden-versions:hover,
.page-package-details #show-hidden-versions:focus {
  text-decoration: none;
}
.page-package-details .package-details-info .ms-Icon-ul li {
  margin-bottom: 15px;
}
.page-package-details .owner-list li {
  margin-bottom: 5px;
}
.page-package-details .install-tabs {
  font-size: 0.8em;
}
.page-package-details .install-tabs ul {
  margin-left: 0;
}
.page-package-details .install-tabs .nav-tabs > li {
  margin-left: 5px;
  padding: 0;
}
.page-package-details .install-tabs .nav-tabs > li > a {
  color: #000;
  background-color: #eaeaea;
  border: 0;
  padding: 5px 10px;
  margin: 0;
}
.page-package-details .install-tabs .nav-tabs > li.active > a {
  background-color: #002440;
  border: 0;
  color: #fff;
}
.page-package-details .install-tabs .tab-pane {
  display: table;
}
.page-package-details .install-tabs .tab-pane .install-script {
  padding: 8px 10px;
  display: table-cell;
  width: 100%;
  background-color: #002440;
  font-family: Consolas, Menlo, Monaco, "Courier New", monospace;
  color: #fff;
}
.page-package-details .install-tabs .tab-pane .install-script span::before {
  content: "PM> ";
}
.page-package-details .install-tabs .tab-pane .copy-button {
  display: table-cell;
}
.page-sign-in .provider-button {
  margin-top: 70px;
  line-height: 2em;
}
.page-sign-in .provider-button img {
  margin-bottom: 1px;
}
.page-sign-in .or-row {
  margin: 30px;
}
